<template>
  <div class="detail">
    <div class="operation-button">
      <el-button
        :type="isPlaying ? 'primary' : 'success'"
        plain
        @click="resetSeat()"
        :disabled="isPlaying"
        :icon="isPlaying ? 'el-icon-basketball' : 'el-icon-guide'"
        >{{ isPlaying ? "游戏中..." : "开局选座" }}</el-button
      >
      <div style="width: 60%">
        <el-select
          v-model="selectPlayers"
          placeholder="请选择或添加玩家"
          multiple
          class="select-players"
          popper-class="selectCls"
          filterable
          :disabled="isPlaying"
        >
          <el-option-group
            v-for="group in accounts"
            :key="group.name"
            :label="group.name"
          >
            <el-option
              v-for="item in group.subAccounts"
              :key="item.value"
              :label="item.name"
              :value="item.value"
              :disabled="disabledFun(item.value)"
            >
            </el-option>
          </el-option-group>
        </el-select>
        <el-button
          type="primary"
          @click="outerVisible = true"
          style="margin-left: 10px"
          :disabled="isPlaying"
          >添加玩家</el-button
        >
      </div>

      <el-button
        type="info"
        plain
        icon="el-icon-s-data"
        @click="scoreboardDrawer = true"
        >查看总排行榜</el-button
      >
    </div>
    <div class="content" v-if="players.length !== 0">
      <el-row :gutter="20">
        <el-col :span="12">
          <el-table :data="playersSort" stripe style="width: 100%">
            <el-table-column prop="seatNum" label="座位号" width="80">
            </el-table-column>
            <el-table-column
              prop="name"
              label="玩家"
              width="200"
              class-name="player-cell"
            >
              <template slot-scope="scope">
                <div class="avatar-box" style="height: 30px">
                  <el-avatar
                    v-if="scope.row.avatar"
                    :size="30"
                    :src="scope.row.avatar"
                  ></el-avatar>
                  <el-avatar v-else :size="30" class="avatar-empty">{{
                    scope.row.name
                  }}</el-avatar>
                </div>
                <span style="margin-left: 10px">{{ scope.row.name }}</span>
              </template>
            </el-table-column>
            <el-table-column prop="chips" label="剩余筹码">
              <template slot-scope="scope">
                <el-input
                  v-model="scope.row.chips"
                  placeholder="请输入该玩家手中剩余筹码"
                  size="mini"
                  clearable
                  @input="limitInput(scope.row)"
                  class="chips"
                ></el-input>
              </template>
            </el-table-column>
          </el-table>
          <div class="account-button" v-if="this.playersSort.length !== 0">
            <el-button type="primary" round @click="settleAccount"
              >结算本局</el-button
            >
            <el-switch
              v-model="humaneModel"
              active-text="人道模式结算"
              class="humane-model"
            >
            </el-switch>
          </div>
        </el-col>
        <el-col :span="12">
          <el-card class="box-card">
            <div
              slot="header"
              class="clearfix"
              style="text-align: center; color: blueviolet; font-weight: bold"
            >
              <span>今日排行榜</span>
            </div>
            <el-table :data="players" style="width: 100%">
              <el-table-column label="排名" width="50">
                <template slot-scope="scope">
                  <img
                    v-if="scope.$index === 0"
                    class="medal"
                    src="@/assets/diyiming.svg"
                  />
                  <img
                    v-else-if="scope.$index === 1"
                    class="medal"
                    src="@/assets/dierming.svg"
                  />
                  <img
                    v-else-if="scope.$index === 2"
                    class="medal"
                    src="@/assets/disanming.svg"
                  />
                  <span v-else style="font-size: 13px; margin-left: 5px">{{
                    scope.$index + 1
                  }}</span>
                </template>
              </el-table-column>
              <el-table-column
                prop="name"
                label="玩家"
                width="180"
                class-name="player-cell"
              >
                <template slot-scope="scope">
                  <div class="avatar-box">
                    <img
                      v-if="scope.$index === 0"
                      class="diyiming-avatar"
                      src="@/assets/diyiming2.svg"
                    />
                    <img
                      v-else-if="scope.$index === 1"
                      class="dierming-avatar"
                      src="@/assets/dierming1.svg"
                    />
                    <img
                      v-else-if="scope.$index === 2"
                      class="dierming-avatar"
                      src="@/assets/disanming1.svg"
                    />
                    <el-avatar
                      v-if="scope.row.avatar"
                      :size="30"
                      :src="scope.row.avatar"
                    ></el-avatar>
                    <el-avatar v-else :size="30" class="avatar-empty">{{
                      scope.row.name
                    }}</el-avatar>
                  </div>
                  <span style="margin-left: 10px">{{ scope.row.name }}</span>
                </template>
              </el-table-column>
              <el-table-column label="胜/负" :formatter="formatterResult">
              </el-table-column>
              <el-table-column label="胜率">
                <template slot-scope="scope">
                  <span :class="addRateClass(scope.row, players)">{{
                    formatterWinRate(scope.row)
                  }}</span>
                </template>
              </el-table-column>
              <el-table-column label="状态" :formatter="formatterState">
                <template slot-scope="scope">
                  <span :class="addStateClass(scope.row, players)">{{
                    formatterState(scope.row)
                  }}</span>
                </template>
              </el-table-column>
              <el-table-column prop="score" label="积分"> </el-table-column>
            </el-table>
          </el-card>
        </el-col>
      </el-row>
    </div>
    <el-drawer
      title="结算明细"
      :visible.sync="drawer"
      direction="rtl"
      :before-close="handleClose"
    >
      <template v-if="resultInfo.length !== 0">
        <el-result icon="success"></el-result>
        <div
          v-for="(info, index) in resultInfo"
          :key="index"
          style="margin-bottom: 10px"
        >
          <span style="margin-left: 20px">{{ info }}</span>
          <el-divider></el-divider>
        </div>
      </template>
      <el-result v-else icon="info" title="无需转账操作"></el-result>
      <div class="drawer__footer">
        <el-button @click="cancelInfo">关 闭</el-button>
        <el-button type="danger" @click="gameAgain">再来一局</el-button>
      </div>
    </el-drawer>
    <el-drawer :visible.sync="scoreboardDrawer" :with-header="false" size="50%">
      <el-card class="box-card">
        <div
          slot="header"
          class="clearfix"
          style="text-align: center; color: blueviolet; font-weight: bold"
        >
          <span>总排行榜</span>
        </div>
        <el-table :data="allPlayers" style="width: 100%">
          <el-table-column label="排名" width="50">
            <template slot-scope="scope">
              <img
                v-if="scope.$index === 0"
                class="medal"
                src="@/assets/diyiming.svg"
              />
              <img
                v-else-if="scope.$index === 1"
                class="medal"
                src="@/assets/dierming.svg"
              />
              <img
                v-else-if="scope.$index === 2"
                class="medal"
                src="@/assets/disanming.svg"
              />
              <span v-else style="font-size: 13px; margin-left: 5px">{{
                scope.$index + 1
              }}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="name"
            label="玩家"
            width="180"
            class-name="player-cell"
          >
            <template slot-scope="scope">
              <div class="avatar-box">
                <img
                  v-if="scope.$index === 0"
                  class="diyiming-avatar"
                  src="@/assets/diyiming2.svg"
                />
                <img
                  v-else-if="scope.$index === 1"
                  class="dierming-avatar"
                  src="@/assets/dierming1.svg"
                />
                <img
                  v-else-if="scope.$index === 2"
                  class="dierming-avatar"
                  src="@/assets/disanming1.svg"
                />
                <el-avatar
                  v-if="scope.row.avatar"
                  :size="30"
                  :src="scope.row.avatar"
                ></el-avatar>
                <el-avatar v-else :size="30" class="avatar-empty">{{
                  scope.row.name
                }}</el-avatar>
              </div>
              <span style="margin-left: 10px">{{ scope.row.name }}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="score"
            label="胜/负"
            :formatter="formatterResult"
          >
          </el-table-column>
          <el-table-column label="胜率">
            <template slot-scope="scope">
              <span :class="addRateClass(scope.row, allPlayers)">{{
                formatterWinRate(scope.row)
              }}</span>
            </template>
          </el-table-column>
          <el-table-column label="状态" :formatter="formatterState">
            <template slot-scope="scope">
              <span :class="addStateClass(scope.row, allPlayers)">{{
                formatterState(scope.row)
              }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="score" label="积分"> </el-table-column>
        </el-table>
      </el-card>
    </el-drawer>
    <el-dialog
      title="添加账号"
      :visible.sync="outerVisible"
      @closed="handleAddAccountClosed"
    >
      <el-form
        ref="accountForm"
        :model="accountForm"
        label-width="80px"
        :rules="rules"
      >
        <div class="add-avatar">
          <div class="avatar-wrap">
            <el-avatar
              v-if="currentAccountAvatar"
              :size="100"
              :src="currentAccountAvatar"
            ></el-avatar>
            <el-avatar v-else :size="100">{{
              accountForm.mainAccount
            }}</el-avatar>
            <el-button type="text" @click="uploadPicture(currentAccountAvatar)"
              >更改头像</el-button
            >
          </div>
        </div>
        <el-form-item label="主账号" class="main-account" prop="mainAccount">
          <el-select
            v-model="accountForm.mainAccount"
            placeholder="请选择主账号"
          >
            <el-option
              v-for="item in mainAccounts"
              :key="item.label"
              :label="item.label"
              :value="item.label"
            ></el-option>
          </el-select>
          <el-button icon="el-icon-user-solid" @click="openAddAccount"
            >添加</el-button
          >
        </el-form-item>
        <el-form-item class="sub-account" label="小号名称" prop="subAccount">
          <el-input v-model="accountForm.subAccount"></el-input>
        </el-form-item>
      </el-form>
      <el-dialog
        width="30%"
        title="主账号名称"
        :visible.sync="innerVisible"
        append-to-body
      >
        <el-input
          placeholder="请输入主账号名称"
          v-model="accountInput"
          clearable
        >
        </el-input>
        <div slot="footer" class="add-account-footer">
          <el-button type="primary" @click="addMainAccount">添 加</el-button>
        </div>
      </el-dialog>
      <div slot="footer" class="dialog-footer">
        <el-button @click="outerVisible = false">取 消</el-button>
        <el-button type="primary" @click="addAccount">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 剪裁组件弹窗 -->
    <el-dialog
      title="裁剪图片"
      :visible.sync="cropperModel"
      width="950px"
      center
    >
      <CropperImage
        v-if="cropperModel"
        :Name="cropperName"
        @uploadImgSuccess="handleUploadSuccess"
        ref="child"
      ></CropperImage>
    </el-dialog>
  </div>
</template>
<script>
import { Player } from "../core/Player";
import { cloneDeep } from "lodash-es";
import CropperImage from "./CropperImage.vue";

export default {
  components: {
    CropperImage,
  },
  data() {
    return {
      rules: {
        mainAccount: [
          { required: true, message: "请选择一个主账号", trigger: "change" },
        ],
        subAccount: [
          {
            required: true,
            message: "请输入小号名称",
            trigger: "blur",
          },
        ],
      },
      currentAccountAvatar:
        "https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png",
      cropperModel: false,
      cropperName: "", //默认图片参数,
      accountInput: "",
      accountForm: {
        mainAccount: "",
        subAccount: "",
      },
      outerVisible: false,
      innerVisible: false,
      accounts: JSON.parse(localStorage.getItem("accounts")),
      selectPlayers: [],
      playerOptions: [],
      players: [],
      playersSort: [],
      drawer: false,
      baseChips: 10,
      resultInfo: [],
      humaneModel: false,
      isPlaying: false,
      scoreboardDrawer: false,
      allPlayers: JSON.parse(localStorage.getItem("allPlayers")),
    };
  },
  computed: {
    mainAccounts() {
      return this.accounts.map((item) => {
        return {
          label: item.name,
          avatar: item.avatar,
        };
      });
    },
    selectPlayersMap() {
      const selectPlayersMap = [];
      this.accounts.forEach((item) => {
        item.subAccounts.forEach((sub) => {
          selectPlayersMap.push({
            name: sub.name,
            pid: item.name,
            avatar: item.avatar,
          });
        });
      });
      return selectPlayersMap;
    },
  },
  watch: {
    "accountForm.mainAccount": {
      handler(newValue) {
        if (newValue) {
          const currentAccount = this.mainAccounts.find(
            (item) => item.label === newValue
          );
          this.currentAccountAvatar = currentAccount.avatar;
        } else {
          this.currentAccountAvatar =
            "https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png";
        }
      },
      deep: true,
    },
  },
  created() {
    if (!this.accounts) {
      // 本地没有任何数据时,初始化默认账号数据
      this.accounts = [
        {
          name: "范宇哲",
          avatar: "",
          subAccounts: [
            {
              name: "沙河彭于晏",
              value: "范宇哲-沙河彭于晏",
            },
          ],
        },
        {
          name: "丁克实",
          avatar: "",
          subAccounts: [
            {
              name: "黑猫警长",
              value: "丁克实-黑猫警长",
            },
          ],
        },
        {
          name: "陈一正",
          avatar: "",
          subAccounts: [
            {
              name: "沙河海森堡",
              value: "陈一正-沙河海森堡",
            },
          ],
        },
        {
          name: "张经伦",
          avatar: "",
          subAccounts: [
            {
              name: "沙河周杰伦",
              value: "张经伦-沙河周杰伦",
            },
          ],
        },
        {
          name: "李骜",
          avatar: "",
          subAccounts: [
            {
              name: "特异功能",
              value: "李骜-特异功能",
            },
          ],
        },
        {
          name: "雷宇",
          avatar: "",
          subAccounts: [
            {
              name: "崔各庄伽利略",
              value: "雷宇-崔各庄伽利略",
            },
          ],
        },
        {
          name: "洋洋",
          avatar: "",
          subAccounts: [
            {
              name: "沙河迪丽热巴",
              value: "洋洋-沙河迪丽热巴",
            },
          ],
        },
      ];
      localStorage.setItem("accounts", JSON.stringify(this.accounts));
    } else {
      this.accounts = JSON.parse(localStorage.getItem("accounts"));
    }
  },
  methods: {
    createPlayer(seatNum, name, avatar, pid) {
      const chips = this.baseChips;
      const raceResult = {
        win: 0,
        lose: 0,
        deuce: 0,
      };
      const raceDate = new Date().getTime();
      const score = 0;
      return new Player(
        seatNum,
        name,
        chips,
        raceResult,
        raceDate,
        score,
        avatar,
        pid
      );
    },
    confirmPlayers(players, isAll = false) {
      let selectPlayers = null;
      selectPlayers = this.selectPlayers.map((item) => {
        let subName = item.split("-")[1];
        const curPlayer = this.selectPlayersMap.find(
          (spm) => spm.name == subName
        );

        let pid = curPlayer.pid;
        let avatar = curPlayer.avatar;
        if (isAll) {
          let name = item.split("-")[0];
          return { name, avatar, pid };
        } else {
          return { name: subName, avatar, pid };
        }
      });
      selectPlayers.forEach((sp, index) => {
        const isHave = players.findIndex((item) => item.name == sp.name);
        if (isHave === -1) {
          players.push(this.createPlayer(index, sp.name, sp.avatar, sp.pid));
        }
      });
    },
    resetSeat() {
      const selectPlayers = this.selectPlayers.map((item) => {
        const realName = item.split("-")[1];
        return realName;
      });
      if (selectPlayers.length === 0) {
        this.$message.warning("请先选择玩家");
        return;
      }
      this.playersSort = [];
      this.isPlaying = true;
      this.confirmPlayers(this.players);
      // 生成一个随机数组
      const randomArr = this.shuffle(
        selectPlayers.map((item, index) => index + 1)
      );

      selectPlayers.forEach((playerName, index) => {
        const player = cloneDeep(this.players).find(
          (p) => p.name == playerName
        );
        player.seatNum = randomArr[index]; // 重置座位
        player.chips = this.baseChips; // 重置筹码
        this.playersSort.push(player);
      });

      // 按升序排列
      this.playersSort.sort((a, b) => a.seatNum - b.seatNum);
    },
    settleAccount() {
      const isFull = this.playersSort.findIndex((player) => {
        return player.chips === "";
      });
      if (isFull !== -1) {
        this.$message.warning("请填写好所有玩家的剩余筹码");
        return;
      }
      const inputChips = this.playersSort
        .map((player) => parseFloat(player.chips))
        .reduce((a, b) => parseFloat(((a * 100 + b * 100) / 100).toFixed(2)));
      const correctChips = this.baseChips * this.playersSort.length;
      if (inputChips === correctChips) {
        this.$confirm("结算前请填写好玩家的剩余筹码, 是否继续结算?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        })
          .then(() => {
            this.outputResult(); // 输出转账结果
            this.updateRanking(this.players); // 更新今日排行榜
            this.updateScoreboard(); // 更新总排行榜
          })
          .catch(() => {});
      } else {
        const diffChips = (
          (correctChips * 100 - inputChips * 100) /
          100
        ).toFixed(2);
        this.$message.error(
          `输入的剩余筹码总额为${inputChips}，${
            diffChips > 0 ? "少" : "多"
          }于总筹码${Math.abs(diffChips)}, 请先确认剩余筹码后再进行结算`
        );
      }
    },
    outputResult() {
      this.drawer = true;
      const winners = [];
      const losers = [];
      this.playersSort.forEach((player) => {
        player["actualChips"] = parseFloat(
          ((player.chips * 100 - this.baseChips * 100) / 100).toFixed(2)
        );
        if (player.actualChips > 0) {
          player["result"] = "win";
          winners.push(cloneDeep(player));
        } else if (player.actualChips < 0) {
          player["result"] = "lose";
          losers.push(cloneDeep(player));
        } else {
          player["result"] = "deuce";
        }
      });
      this.allot(winners, losers);
    },
    allot(winners, losers) {
      // 给winners降序排序
      winners.sort((a, b) => b.actualChips - a.actualChips);
      if (this.humaneModel) {
        // 给losers降序排列（人道模式）
        losers.sort((a, b) => a.actualChips - b.actualChips);
      } else {
        losers.sort((a, b) => b.actualChips - a.actualChips);
        if (losers[0].actualChips === 0) {
          losers.splice(0, 1);
        }
      }
      if (winners[0].actualChips === 0) {
        return;
      } else {
        const tolerance =
          Math.abs(winners[0].actualChips) - Math.abs(losers[0].actualChips) <=
          0
            ? Math.abs(winners[0].actualChips)
            : Math.abs(losers[0].actualChips); // 转账的公差
        winners[0].actualChips = parseFloat(
          ((winners[0].actualChips * 100 - tolerance * 100) / 100).toFixed(2)
        ); // 解决js小数精度问题
        losers[0].actualChips = parseFloat(
          ((losers[0].actualChips * 100 + tolerance * 100) / 100).toFixed(2)
        );
        const outputInfo = `${losers[0].name} 向 ${winners[0].name} 转账 ${tolerance}元`;
        this.resultInfo.push(outputInfo);
        this.allot(winners, losers);
      }
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(() => {
          done();
          this.cancelInfo();
        })
        .catch(() => {});
    },
    shuffle(array) {
      // 洗牌算法
      var j, x, i;
      for (i = array.length; i; i--) {
        j = Math.floor(Math.random() * i);
        x = array[i - 1];
        array[i - 1] = array[j];
        array[j] = x;
      }
      return array;
    },
    limitInput(value) {
      //限制只能输入两位小数
      const maxChips = this.baseChips * this.playersSort.length;
      if (value.chips > maxChips) {
        value.chips = maxChips;
      }
      if (value.chips < 0) {
        value.chips = 0;
      }
      value.chips =
        ("" + value.chips) // 第一步：转成字符串
          .replace(/[^\d^.]+/g, "") // 第二步：把不是数字，不是小数点的过滤掉
          .replace(/^0+(\d)/, "$1") // 第三步：第一位0开头，0后面为数字，则过滤掉，取后面的数字
          .replace(/^\./, "0.") // 第四步：如果输入的第一位为小数点，则替换成 0. 实现自动补全
          .match(/^\d*(\.?\d{0,2})/g)[0] || ""; // 第五步：最终匹配得到结果 以数字开头，只有一个小数点，而且小数点后面只能有0到2位小数
    },
    updateRanking(players, isAll = false) {
      // 更新排行榜
      let playersSort = null;
      playersSort = cloneDeep(this.playersSort).map((item) => {
        if (isAll) {
          item.name = item.pid;
        }
        return item;
      });
      players.forEach((player) => {
        const currentPlayer = playersSort.find((p) => player.name === p.name);
        if (currentPlayer) {
          // 计算今日累计得分
          const playerActualChips = currentPlayer.actualChips;
          player.score = parseFloat(
            ((playerActualChips * 100 + player.score * 100) / 100).toFixed(2)
          );
          // 计算今日胜负结果
          player.raceResult[currentPlayer.result]++;
          // 计算连胜状态，出现deuce状态时, 保持上一次状态不变
          if (currentPlayer.result !== "deuce") {
            if (currentPlayer.result === player.lastState) {
              player.stateTimes++;
            } else {
              player.stateTimes = 1;
            }
            player.lastState = currentPlayer.result; // 记录最后一次的比赛结果
          }
          // 更新头像
          player.avatar = currentPlayer.avatar;
        }
      });
      players.sort((a, b) => b.score - a.score);
    },
    updateScoreboard() {
      // 更新总排行榜
      if (!this.allPlayers) {
        // 本地没有任何数据时
        const allPlayers = cloneDeep(this.players).map((player) => {
          player.name = player.pid;
          return player;
        });
        this.allPlayers = cloneDeep(allPlayers);
        localStorage.setItem("allPlayers", JSON.stringify(this.allPlayers));
      } else {
        this.confirmPlayers(this.allPlayers, true);
        this.updateRanking(this.allPlayers, true);
        localStorage.setItem("allPlayers", JSON.stringify(this.allPlayers));
      }
    },
    formatterResult(row) {
      return `${row.raceResult.win} / ${row.raceResult.lose}`;
    },
    formatterWinRate(row) {
      const rate = this.computedRate(row);
      return `${rate}%`;
    },
    computedRate(player) {
      const total =
        player.raceResult.win +
        player.raceResult.lose +
        player.raceResult.deuce;
      const rate =
        total && parseFloat(((player.raceResult.win / total) * 100).toFixed(1));
      return rate;
    },
    addRateClass(val, players) {
      const rates = players.map((player) => {
        return this.computedRate(player);
      });
      const maxRate = Math.max(...rates);
      const minRate = Math.min(...rates);
      if (this.computedRate(val) === maxRate) {
        return "highest";
      } else if (this.computedRate(val) === minRate) {
        return "lowest";
      } else {
        return "";
      }
    },
    formatterState(row) {
      return `${row.stateTimes}连${row.lastState === "lose" ? "败" : "胜"}`;
    },
    addStateClass(row, players) {
      const loseStateTimes = players
        .filter((item) => {
          return item.lastState === "lose";
        })
        .map((item) => item.stateTimes);

      const winStateTimes = players
        .filter((item) => {
          return item.lastState === "win";
        })
        .map((item) => item.stateTimes);
      const maxLoseTimes = Math.max(...loseStateTimes);
      const maxWinTimes = Math.max(...winStateTimes);
      if (row.lastState === "win" && row.stateTimes === maxWinTimes) {
        return "highest";
      }
      if (row.lastState === "lose" && row.stateTimes === maxLoseTimes) {
        return "lowest";
      }
      return "";
    },
    handleUploadSuccess(imgData) {
      //上传图片
      this.mainAccounts.forEach((item) => {
        if (item.label === this.accountForm.mainAccount) {
          item.avatar = imgData;
        }
      });
      this.currentAccountAvatar = imgData;
      const accountIndex = this.accounts.findIndex(
        (item) => item.name === this.accountForm.mainAccount
      );
      if (accountIndex !== -1) {
        this.accounts[accountIndex].avatar = this.currentAccountAvatar;
        // 存储图片
        localStorage.setItem("accounts", JSON.stringify(this.accounts));
      }
      this.cropperModel = false;
    },
    //打开文件裁剪组件，并传入默认参数
    uploadPicture(name) {
      this.cropperName = name;
      this.cropperModel = true;
    },
    openAddAccount() {
      this.accountInput = "";
      this.innerVisible = true;
    },
    addMainAccount() {
      if (!this.accountInput) {
        this.$message.warning("请输入账号名称");
      } else {
        if (this.isNameRepeat(this.accountInput)) {
          this.$message.error("该名称已存在，请更换名称");
        } else {
          this.mainAccounts.push({
            label: this.accountInput,
            avatar: "",
          });
          this.accountForm.mainAccount = this.accountInput;
          this.innerVisible = false;
        }
      }
    },
    isNameRepeat(name) {
      const allNames = [];
      this.accounts.forEach((item) => {
        allNames.push(item.name);
        if (item.subAccounts && item.subAccounts.length > 0) {
          item.subAccounts.forEach((subItem) => {
            allNames.push(subItem.name);
          });
        }
      });
      return allNames.indexOf(name) !== -1;
    },
    addAccount() {
      if (this.isNameRepeat(this.accountForm.subAccount)) {
        this.$message.error("该小号名称已存在，请重新填写");
        return;
      }
      this.$refs.accountForm.validate((valid) => {
        if (valid) {
          const accountIndex = this.accounts.findIndex(
            (item) => item.name === this.accountForm.mainAccount
          );
          const subAccountValue = `${this.accountForm.mainAccount}-${this.accountForm.subAccount}`;
          if (accountIndex === -1) {
            // 添加了新的主账号
            this.accounts.push({
              name: this.accountForm.mainAccount,
              avatar: this.currentAccountAvatar,
              subAccounts: [
                {
                  name: this.accountForm.subAccount,
                  value: `${this.accountForm.mainAccount}-${this.accountForm.subAccount}`,
                },
              ],
            });
          } else {
            // 添加小号
            this.accounts[accountIndex].subAccounts.push({
              name: this.accountForm.subAccount,
              value: subAccountValue,
            });
          }
          localStorage.setItem("accounts", JSON.stringify(this.accounts));
          const sameGroupIndex = this.selectPlayers.findIndex((item) => {
            const itemPid = item.split("-")[0];
            return itemPid === this.accountForm.mainAccount;
          });
          if (sameGroupIndex !== -1) {
            this.selectPlayers.splice(sameGroupIndex, 1, subAccountValue);
          } else {
            this.selectPlayers.push(subAccountValue);
          }
          this.outerVisible = false;
        } else {
          return false;
        }
      });
    },
    handleAddAccountClosed() {
      this.$refs.accountForm.resetFields();
    },
    disabledFun(val) {
      const valueId = val.split("-")[0];
      const valueName = val.split("-")[1];
      return this.selectPlayers.some((item) => {
        const itemId = item.split("-")[0];
        const itemName = item.split("-")[1];
        return valueId == itemId && valueName !== itemName;
      });
    },
    gameAgain() {
      // 再来一局
      this.resultInfo = []; // 清空转账消息列表
      this.resetSeat();
      this.drawer = false;
    },
    cancelInfo() {
      this.resultInfo = []; // 清空转账消息列表
      this.playersSort = [];
      this.isPlaying = false;
      this.drawer = false;
    },
  },
};
</script>
